* {
  font-family: "Montserrat", sans-serif;
}

.logo {
  background-color: white;
}

/* los section new tenemos que recontar las secciones para que tenga sentido donde la ubicamos.   */
.section-new {
  display: flex;
  margin-top: 100px;
  justify-content: flex-start;
  height: 60vh;
}

.section-two-title {
  font-size: 16px;
  font-weight: 600;
  background-color: #7510f7;
  text-align: center;
  margin: 0;
  padding: 50px;
  color: white;
}

#container {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  /* grid-template-rows: 50px; */
  grid-gap: 5px;
  box-sizing: border-box;
  height: 20vh;
  width: 100%;
  align-content: center;
  background-color: #8ca0ff;
  padding: 10px;
}

/* height: para juntar ambos containers
padding-top: el margen del container del titulo
