/* scroll menu styles ///////////*/
/* paddig-top utilice to center items. */

div.scrollmenu {
  background-color: #7510f7;
  overflow: auto;
  white-space: nowrap;
  height: 160px;
  /* padding-top: 80px; */
  text-align: center;
}

div.scrollmenu p {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 20px;
  text-decoration: none;
}

