/* mobile displays, aqui es donde el texto se deberia centrar. Para todas las demas resoluciones el texto deberia ser el mismo.*/
@media screen and (max-width: 414px) {
  /* sujetos a ser eliminados por pertenecer a section-1 */
  .left-col-title {
    text-align: center;
  }
  .right-col-img {
    order: -1;
  }
  .section-five {
    height: 90vh;
  }
}

@media screen and (max-width: 600px) {
  * {
    text-align: center;
  }

  .section-five {
    height: 120vh;
  }

  .right-col-img {
    order: -1;
  }

  .section-new {
    height: 90vh;
  }
}

@media screen and (max-width: 800px) {
  /* title sec-one top margin  */
  .header {
    margin-top: 0px;
    margin-left: 0px;
  }

  .container {
    overflow-x: auto;
    max-width: 700px;
  }

  .left-col-title {
    margin-left: 0px;
  }
}

